import {SceneReactor} from "./scene";
import {JointTypes} from "../constants";
import * as THREE from 'three';
import * as mjs from 'mathjs';
import {HingeJoint} from "../mechanisms/hingeJoint";


class HingeBody {
  // first_arg = derivative w.r.t to this or other body
  constructor(first_arg, this_body, this_local, other_body, other_local) {
    this.first_arg = first_arg;
    this.other_body = other_body;
    this.other_local = other_local;
    this.this_body = this_body;
    this.this_local = this_local;
    this.weight = 1;
  }

  c_body() {
    let p1 = this.this_body.toWorld(this.this_local);
    let p2 = this.other_body.toWorld(this.other_local);
    let s1 = mjs.subtract(p1,p2);
    return this.weight*0.5*mjs.dot(s1, s1);
  }

  // gradient only w.r.t to state of current rigid body!
  dc_dbody() {
    let p1 = this.this_body.toWorld(this.this_local);
    let p2 = this.other_body.toWorld(this.other_local);
    let s1 = mjs.subtract(p1,p2);

    // derivative w.r.t to this_body or other_body
    let dp_dxi = (!this.first_arg) ?
       mjs.transpose(mjs.unaryMinus(this.other_body.dWorld_dxi(this.other_local))) :
         mjs.transpose(this.this_body.dWorld_dxi(this.this_local));

    // is this true ? dp2_dxi = 2x3 so be sure we make correct stuff here
    let grad_body = mjs.multiply(dp_dxi, s1);

    return mjs.multiply(this.weight, grad_body);
  }

}

class HingeReactor extends SceneReactor {

  constructor(sceneContainer) {
    super(sceneContainer);
    // this is for state case
    this.hingeProps = [];

    // this is for body dependant
    this.hingePropsBody = [];

    this.jointType = JointTypes.HINGE;
  }

  initDatGui(gui) {
    gui.addFolder("Hinge")
  }

  mouseDown(event) {
    if (event.intersects.length === 1) {
      let i = event.intersects[0];
      let link = this.sceneContainer.getLink(i.object);

      if (this.hingeProps.length > 0) {
        if (this.hingeProps[0] === link.bodyIdx) {
          throw "Not possible to connect hinge to same object twice";
        }
      }

      console.log(i);
      var shape = new THREE.Shape();

      let localP = i.object.worldToLocal(new THREE.Vector3(event.x, event.y, i.object.position.z)); //new THREE.Vector3(event.x - i.object.position.x, 0, 0);
      localP.y = 0;
      shape.ellipse(localP.x, localP.y , 10, 10, 0, 2*Math.PI);

      var extrudeSettings = {
        steps: 2,
        depth: 16,
        bevelEnabled: true,
        bevelThickness: 1,
        bevelSize: 1,
        bevelOffset: 0,
        bevelSegments: 1
      };

      let geometry = new THREE.ExtrudeGeometry( shape, extrudeSettings );
      let material = new THREE.MeshBasicMaterial( { color: 0x00ff00 } );
      let mesh = new THREE.Mesh( geometry, material ) ;

      this.hingeProps.push(link.bodyIdx, localP);
      this.hingePropsBody.push(link, localP);

      if (this.hingeProps.length === 4) {
        this.createHinge();
      }

      console.log(mesh);
      //scene.add( circle );
      i.object.add(mesh);
      //i.object.material.color.set(0xff0000);
    } else if (event.intersects.length > 1) {
      console.log("hingeReactor.js: Number of Intersects should not be greater then 1!");
    }
  }

  createHinge() {
    // this is for the use case where we calculate the gradient globally on a defined state vector
    let hingeJoint = new HingeJoint(this.hingeProps[0], this.hingeProps[2], this.hingeProps[1], this.hingeProps[3]);
    this.sceneContainer.addJoint(hingeJoint);
    this.hingeProps = [];

    // this is for the use case where we calculate the gradient w.r.t to the body
    // now we create 2 hinge constraints
    let b1 = this.hingePropsBody[0];
    let b1_plocal = this.hingePropsBody[1];
    let b2 = this.hingePropsBody[2];
    let b2_plocal = this.hingePropsBody[3];

    b1.constraints.push(new HingeBody(true, b1, b1_plocal, b2, b2_plocal));
    b2.constraints.push(new HingeBody(false, b1, b1_plocal, b2, b2_plocal));
  }
}

export {HingeReactor}
