import {SceneReactor} from "./scene";
import {JointTypes} from "../constants";
import * as mjs from 'mathjs'
import {FixJoint} from "../mechanisms/fixJoint";


class FixConstraintBody {
  constructor(this_body, fixed_pos, fixed_angle) {
    this.this_body = this_body;
    this.weight = 100;
    this.fixed_pos = this.to_matrix(fixed_pos);
    this.fixed_angle = fixed_angle;
  }

  c_body() {
    let p = this.this_body.getPos();
    let theta = this.this_body.getTheta();

    let s1 = mjs.subtract(p, this.fixed_pos);
    let s2 = theta - this.fixed_angle;
    return this.weight*0.5*(mjs.dot(s1, s1) + s2*s2);
  }

  to_matrix(p) {
    return mjs.matrix([p.x, p.y]);
  }

  // gradient only w.r.t to state of "this" rigid body!
  dc_dbody() {
    // only 3 because pos.x, pos.y, theta, we don't need to know anything about the global state!! so nice
    let grad = mjs.matrix(mjs.zeros(3));

    let xp = this.to_matrix(this.this_body.getPos());


    let c1 = (mjs.subtract(xp, this.fixed_pos));
    let c2 = this.this_body.getTheta() - this.fixed_angle;

    grad.subset(mjs.index([0, 1]), mjs.multiply(c1, this.weight));
    grad.subset(mjs.index(2), this.weight*c2);
    return grad
  }




}

class FixConstraintReactor extends SceneReactor {

  constructor(sceneContainer) {
    super(sceneContainer);
    this.hingeProps = []
    this.jointType = JointTypes.FIX;
  }

  initDatGui(gui) {
    gui.addFolder("Fix Constraint")
  }


  mouseDown(event) {
    if (event.intersects.length > 1) {
      console.log("fixReactor.js: Number of Intersects should not be greater then 1!");
      return;
    }
    if (event.intersects.length === 1) {
      let i = event.intersects[0];
      let link = this.sceneContainer.getLink(i.object);

      if (event.is_left_click) {
        // remove any old fix constraint first
        this.sceneContainer.removeFixJoint(link.bodyIdx);

        let fixJoint = new FixJoint(link.bodyIdx, link.getPos(), link.getTheta());
        this.sceneContainer.addJoint(fixJoint);

        link.constraints.push(new FixConstraintBody(link, link.getPos(), link.getTheta()));

        i.object.material.color.set(0xff0000);
      } else {
        this.sceneContainer.removeFixJoint(link.bodyIdx);
        i.object.material.color.set(0x000000);
      }
    }
  }

}

export {FixConstraintReactor}
