import {Joint} from "./joint";
import {JointTypes} from "../constants";
import * as mjs from "mathjs";


class HingeJoint extends Joint {
  constructor(body1, body2, local1, local2) {
    super();
    this.body1 = body1;
    this.body2 = body2;
    this.local1 = local1;
    this.local2 = local2;
  }

  printBody() {
    console.log("body1: ", this.body1,
      "body2: ", this.body2, "local1: ",  this.local1, "local2: ", this.local2);
  }

  getJointType() {
    return JointTypes.HINGE;
  }

  eval(x) {
    let p1 = super.toWorld(x, this.body1, this.local1);
    let p2 = super.toWorld(x, this.body2, this.local2);
    let s1 = mjs.subtract(p1,p2);
    return this.weight*0.5*mjs.dot(s1, s1);
  }

  deval_dx(x) {
    let grad = mjs.matrix(mjs.zeros(x.size()));

    let p1 = super.toWorld(x, this.body1, this.local1);
    let p2 = super.toWorld(x, this.body2, this.local2);
    let s1 = mjs.subtract(p1,p2);

    let dp1_dxi = mjs.transpose(super.dWorld_dxi(x, this.body1, this.local1));
    let dp2_dxi = mjs.transpose(mjs.unaryMinus(super.dWorld_dxi(x, this.body2, this.local2)));

    // is this true ? dp2_dxi = 2x3 so be sure we make correct stuff here
    let grad_body1 = mjs.multiply(dp1_dxi, s1 );
    let grad_body2 = mjs.multiply(dp2_dxi, s1 );

    grad.subset(mjs.index([3*this.body1, 3*this.body1+1, 3*this.body1+2]), grad_body1);
    grad.subset(mjs.index([3*this.body2, 3*this.body2+1, 3*this.body2+2]), grad_body2);
    return mjs.multiply(this.weight, grad);
  }

  d2eval_dx2_(x) {
    let hess = mjs.matrix(mjs.zeros([x.size()[0], x.size()[0]]));

    let idx = 3*this.bodyId;

    let grad = this.deval_dx(x);

    let i = 3*this.body1;
    let j = 3*this.body2;

    // i,j
    let l, k;
    for (l = 0; l < x.size()[0]; l++) {
      for (k = 0; k < x.size()[0]; k++) {
        let val = grad.subset(mjs.index(l))*grad.subset(mjs.index(k));
        if (l !== k) val = -val;
        hess.subset(mjs.index(l,k), val);
      }
    }

    return hess;
  }

}

export {HingeJoint}
