import {SceneReactor} from "./scene";
import {doing} from "../constants";
import * as mathjs from 'mathjs'
import * as mjs from "mathjs";
import {grad_descent, grad_descent_line_search, random_optimizer, newton_opt} from "../optlib/gradDescent";
import {TransformControls} from "three/examples/jsm/controls/TransformControls";




class SimulationReactor extends SceneReactor {
  constructor(props, transformControl) {
    super(props);
    this.isSimulating = false;
    this.options = {
      update_state: true,
      use_body_gradient: false,
      test_gradients: false};
    // transform control
    this.transformControl = transformControl;

  }

  switchDesignOptions(gui_options) {
    this.isSimulating = gui_options === doing.SIMULATE;

    if (this.isSimulating) {
      //this.transformControl.setMode('rotate');
    }
  }

  mouseDown(event) {
    if (!this.isSimulating) return;

    if (event.is_right_click) {
      this.options.update_state = !this.options.update_state;
    }
    if (event.intersects.length === 0) return;

    this.transformControl.attach(event.intersects[0].object);


  }

  mouseMove(event, isDragging) {
    return; // solving dragging of objs directly in entry.js
    if (!this.isSimulating) return;
    if (event.intersects.length === 0) return;
    if (!isDragging) return;


    let l = this.sceneContainer.getLink(event.intersects[0].object);

    l.setPosition(event.x, event.y, 0);
  }

  initDatGui(gui) {

    gui.addFolder("Simulation").open();
    gui.add(this.options, 'update_state').listen();
    gui.add(this.options, 'use_body_gradient').listen();
    gui.add(this.options, 'test_gradients').listen();

  }

  doing_type() {
    return doing.SIMULATE;
  }


  preRender(gui_options) {
    if (!this.isSimulating) return;

    // simulating steps
    // 1. take current state x = state [b1.pos.x, b1.pos.y, b1.theta1, ..., ]
    // 2. calculate error based on constraints ( O(x))
    // 3. calculate derivative based on constraints dO/dx
    // 4. optimize, optimize, optimize :)
    // 5. after each iteration update pos. of gui ? :)
    let x = []
    this.sceneContainer.links.forEach(l => {
      x.push(...l.getState());
    });

    let x_vec = mathjs.matrix(x);

    let sum = this.sceneContainer.totalJoint.eval(x_vec);
    console.log('sum: ', sum);


    if (this.options.test_gradients) {
      this.sceneContainer.totalJoint.check_gradient(x_vec);
      this.sceneContainer.totalJoint.check_hessian(x_vec);
    }

    if (this.options.update_state) {
      if (!this.options.use_body_gradient) {
        //let x_new = grad_descent_line_search(x_vec, this.sceneContainer.totalJoint).valueOf();
        let x_new = newton_opt(x_vec, this.sceneContainer.totalJoint).valueOf();
        //let x_new = random_optimizer(x_vec, this.sceneContainer.totalJoint).valueOf();
        this.sceneContainer.setState(x_new);
      } else {
        this.sceneContainer.links.forEach(l => l.optimize_gradient_descent());
      }

    }

  }
}


export {SimulationReactor}
