/**
 * entry.js
 *
 * This is the first file loaded. It sets up the Renderer,
 * Scene and Camera. It also starts the render loop and
 * handles window resizes.
 *
 */

import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import {TransformControls} from "three/examples/jsm/controls/TransformControls";
import {doing, JointTypes} from './objects/constants';
import {BodyReactor} from "./objects/reactors/bodyReactor";
import * as dat from 'dat.gui';
import {SimulationReactor} from "./objects/reactors/simulationReactor";
import {TotalJoint} from "./objects/mechanisms/joint";
import {JointReactor} from "./objects/reactors/jointReactor";


var scene, width, height, camera, renderer, controls;

// frustum
let frustumSize = 1000;
let aspect;

var mouseIsPressed, mouseX, mouseY;

let reactors = []

var raycaster = new THREE.Raycaster();

const doing_options = [doing.BODIES, doing.Joints, doing.SIMULATE]

var gui_options = {
  doing: doing.BODIES,
  is_addBodies: function() {
    return this.doing === doing.BODIES;
  }
};

function getCanvasRelativePosition(event) {
  let mouse = new THREE.Vector3(0,0,0);
  mouse.x = ( event.clientX / window.innerWidth ) * 2 - 1;
  mouse.y = - ( event.clientY / window.innerHeight ) * 2 + 1;

  let mouse_intersect = new THREE.Vector2(mouse.x, mouse.y);

  mouse.unproject(camera);

  // check for intersections
  raycaster.setFromCamera( mouse_intersect, camera );
  let intersects = raycaster.intersectObjects(scene.children);

  //console.log(intersects);

  return {
    x: mouse.x,
    y: mouse.y,
    is_left_click: event.which === 1,
    is_right_click: event.which === 3,
    intersects: intersects
  }
}

// transform control
let transControl;


class SceneContainer {
  constructor(scene) {
    this.scene = scene;
    this.links = [];
    this.totalJoint = new TotalJoint();
    this.movingLines = [];
  }

  getLink(threeMesh) {
    console.log(threeMesh);
    let l;
    for (l of sceneContainer.links) {
      console.log(l.cube)
      if (l.cube.uuid === threeMesh.uuid) {
        return l;
      }
    }
  }

  addJoint(joint) {
    this.totalJoint.constraints.push(joint);
  }

  setState(x) {
    this.links.forEach(l => {
      let idx = l.bodyIdx;
      l.setPosition(x[3*idx], x[3*idx+1], 0);
      l.setTheta(x[3*idx + 2]);
    });
  }

  removeFixJoint(link) {
    let idx = this.totalJoint.constraints.findIndex(constraint => {
      if (constraint.getJointType() === JointTypes.FIX) {
        if (constraint.body === link) {
          return true;
        }
      }
      return false;
    });
    if (idx !== -1) {
      this.totalJoint.constraints.splice(idx);
      console.log("removed fix constraint");
    }
  }

}

let sceneContainer = new SceneContainer(scene);

function init() {
  // threejs scene
  scene = new THREE.Scene();
  scene.background = new THREE.Color(0xb0b0b0);

  aspect = window.innerWidth / window.innerHeight;
  // ortho camera for 2D
  camera = new THREE.OrthographicCamera( frustumSize * aspect / - 2, frustumSize * aspect / 2, frustumSize / 2, frustumSize / - 2, 1, 1000 );
  camera.up.set(0,1,0);
  camera.lookAt(new THREE.Vector3(0,0,0));
  camera.position.set(0, 0, 50);

  // Renderer will use canvas takin the whole window
  renderer = new THREE.WebGLRenderer({antialias: true});
  renderer.sortObjects = false;
  renderer.setPixelRatio(window.devicePixelRatio);
  renderer.setSize(width, height);

  // camera orbit
  controls = new OrbitControls( camera, renderer.domElement );

  controls.enableRotate = false;

  // transformControl
  transControl = new TransformControls(camera, renderer.domElement);
  transControl.showZ = false;
  transControl.showX = true;
  transControl.showY = true;
  scene.add(transControl);

  // add grid
  var group = new THREE.Group();
  scene.add(group);

  var helper = new THREE.GridHelper(10000, 100);
  helper.rotation.x = Math.PI / 2;
  group.add(helper);

  // Append camera to page
  document.body.appendChild(renderer.domElement);

  // add resize callback
  window.addEventListener('resize', resize);

  // mouse callbacks
  // rist call global mouse stuff before calling user callbacks
  mouseIsPressed = false, mouseX = 0, mouseY = 0;

  sceneContainer.scene = scene;

  reactors.push(new BodyReactor(sceneContainer));
  reactors.push(new SimulationReactor(sceneContainer, transControl));
  reactors.push(new JointReactor(sceneContainer));

  renderer.domElement.addEventListener('mousedown', function(event) {
    mouseIsPressed = true;
    mousePressed();



    reactors.filter(r => r.doing_type() === gui_options.doing)
      .forEach(r => r.mouseDown(getCanvasRelativePosition(event)));
  });

  renderer.domElement.addEventListener('mouseup', function(event) {
    mouseIsPressed = false;
    reactors.filter(r => r.doing_type() === gui_options.doing)
      .forEach(r => r.mouseUp(getCanvasRelativePosition(event)));
  });


  renderer.domElement.addEventListener('mousemove', function (event) {
    mouseMove();
    if (mouseIsPressed) {
      mouseDragging();
    }
    reactors.filter(r => r.doing_type() === gui_options.doing)
      .forEach(r => r.mouseMove(getCanvasRelativePosition(event), mouseIsPressed));
  });

  renderer.domElement.addEventListener('contextmenu', function (event) {
    event.preventDefault();
  });



  initDatGui();

  reactors.forEach(r => r.switchDesignOptions(gui_options.doing));
}

function mouseDragging() {

}

function initDatGui() {
  var gui = new dat.GUI();
  gui.addFolder("Task");
  gui.add(gui_options, "doing", doing_options).name("Doing Options")
    .onChange(function(new_val) {
      console.log(new_val);
      reactors.forEach(r => r.switchDesignOptions(new_val))
    }).listen();

  var removeLastShape = {rLastShape: function() {
      scene.remove(shapes[shapes.length -1]);
      shapes.pop();
    }};
  gui.add(removeLastShape, "rLastShape");

  reactors.forEach(r => r.initDatGui(gui));

}

function mousePressed() {}
function mouseMove() {}

// resize callback
function resize() {

  aspect = window.innerWidth / window.innerHeight;
  camera.left = - frustumSize * aspect / 2;
  camera.right = frustumSize * aspect / 2;
  camera.top = frustumSize / 2;
  camera.bottom = - frustumSize / 2;

  camera.updateProjectionMatrix();
  renderer.setSize( window.innerWidth, window.innerHeight );
}

// The render callback
function render () {
  requestAnimationFrame( render );

  controls.update();

  reactors.forEach(r => r.preRender(gui_options));
  sceneContainer.movingLines.forEach(l => l.geometry.verticesNeedUpdate = true);

  renderer.render( scene, camera );
};


init();
resize();
render();
