import {JointTypes} from "../constants";
import * as mjs from 'mathjs'

class Joint {

  constructor() {
    this.weight = 1;
  }


  getJointType() {
    return JointTypes.UNDEFINED;
  }

  getPos(x, bodyId) {
    return x.subset(mjs.index([3*bodyId, 3*bodyId+1]));
  }

  getAngle(x, bodyId) {
    return x.subset(mjs.index(3*bodyId + 2));
  }

  get2dRotMatrix(x, bodyId) {
    let theta = this.getAngle(x, bodyId);
    let cosA = mjs.cos(theta);
    let sinA = mjs.sin(theta);
    let m =  mjs.matrix([[cosA, -sinA], [sinA, cosA]]);
    return m;
  }

  get_drot_dxi(x, bodyId) {
    let theta = this.getAngle(x, bodyId);
    let cosA = mjs.cos(theta);
    let sinA = mjs.sin(theta);
    let m =  mjs.matrix([[-sinA, -cosA], [cosA, -sinA]]);
    return m;
  }

  toWorld(x, bodyId, pLocal) {
    let p = mjs.matrix([pLocal.x, pLocal.y]);
    let r1 = mjs.multiply(this.get2dRotMatrix(x, bodyId), p);
    let res = mjs.add(this.getPos(x, bodyId), r1);
    return res;
  }

  // returns dp/dxi where xi = [pos(bodyId).x, pos(bodyId).y, theta(bodyId)]
  // rest of the dxi's would be zero anyway
  // result = [dp/x dp/dy dp/dtheta]
  dWorld_dxi(x, bodyId, pLocal) {
    let p = mjs.matrix([pLocal.x, pLocal.y]);
    let drot_dxi = this.get_drot_dxi(x, bodyId);
    let v1 = mjs.multiply(drot_dxi, p).valueOf();
    return mjs.matrix([[1, 0, v1[0]],
                       [0, 1, v1[1]]]);
  }

  eval(x) {
    throw "Please implement this";
  }


  fd_grad(x) {
    let h = 1e-6;
    let grad = mjs.matrix(mjs.zeros(x.size()));

    let i;
    for (i = 0; i < x.size(); i++) {
      let idx = mjs.index(i);
      let val = x.subset(idx);
      x.subset(idx, val + h);
      let e1 = this.eval(x);
      x.subset(idx, val - h);
      let e2 = this.eval(x);
      x.subset(idx, val);
      grad.subset(idx, (e1 - e2) / (2*h));
    }
    return grad;
  }

  deval_dx(x) {
    return this.fd_grad(x);
  }

  check_gradient(x) {
    let g1 = this.fd_grad(x);
    let g2 = this.deval_dx(x);

    let diff = mjs.norm(mjs.subtract(g1, g2));
    if (diff < 1e-3) {
      console.log('gradient finite diff test ok!');
    } else {
      console.log('FAIL finitie diff test!');
      console.log('finite differences: ', g1.valueOf());
      console.log('calculated grad: ', g2.valueOf());
    }
  }

  fd_hess_complete(x) {
    let h = 1e-4;
    let n = x.size()[0];
    let hess = mjs.matrix(mjs.zeros([n, n]));

    let i,j;
    for (i = 0; i < n; i++) {
      let ei = mjs.matrix(mjs.zeros(n));
      ei.subset(mjs.index(i), h);

      for (j = 0; j < n; j++) {
        let ej = mjs.matrix(mjs.zeros(n));
        ej.subset(mjs.index(j), h);

        let f1 = this.eval(mjs.add(mjs.add(x, ei), ej));
        let f2 = this.eval(mjs.add(x, ei));
        let f3 = this.eval(mjs.add(x, ej));
        let f = this.eval(x);

        let c = (f1 - f2 - f3 + f) / (h*h);
        hess.subset(mjs.index(i,j), c);
      }
    }

    hess = mjs.multiply(0.5, mjs.add(hess, mjs.transpose(hess)));

    return hess;
  }

  check_hessian(x) {
    let h1 = this.fd_hess_complete(x);//this.fd_hess(x);
    let h2 = this.d2eval_dx2(x);

    let diff = mjs.abs(mjs.subtract(h1, h2));
    let i,j; let fail = false;
    for (i = 0; i < x.size()[0]; i++) {
      for (j = 0; j < x.size()[0]; j++) {
        let idx = mjs.index(i, j);
        if (diff.subset(idx) > 1e-3) {
          fail = true;
        }
      }
    }
    if (fail) {
      console.log('FAIL hessian diff test!');
      console.log('hessian fd: ', h1.valueOf());
      console.log('calculated hess: ', h2.valueOf());
    } else {
      console.log('hessian finite diff test ok!');
    }
  }

  fd_hess(x) {
    let h = 1e-6;
    let hess = mjs.matrix(mjs.zeros([x.size()[0], x.size()[0]]));

    let i, j;
    for (i = 0; i < x.size(); i++) {

      let idx = mjs.index(i);
      let val = x.subset(idx);
      x.subset(idx, val + h);
      let e1 = this.deval_dx(x);
      x.subset(idx, val - h);
      let e2 = this.deval_dx(x);
      x.subset(idx, val);

      let grad_fd = mjs.multiply(mjs.subtract(e1, e2), 1/(2*h));
      hess.subset(mjs.index(mjs.range(0, x.size()[0]), i), grad_fd);
    }
    return hess;
  }


  d2eval_dx2(x) {
    return this.fd_hess_complete(x);
  }





}

class TotalJoint extends Joint {
  constructor() {
    super();
    this.constraints = [];
  }

  eval(x) {
    let sum = 0;
    this.constraints.forEach(constraint => {
      sum += constraint.eval(x);
    });
    return sum;
  }

  deval_dx(x) {
    let grad = mjs.matrix(mjs.zeros(x.size()[0]));
    this.constraints.forEach(constraint => {
      grad = mjs.add(grad, constraint.deval_dx(x));
    });
    return grad;
  }

  d2eval_dx2(x) {
    let hess = mjs.matrix(mjs.zeros([x.size()[0], x.size()[0]]));
    this.constraints.forEach(constraint => {
      hess = mjs.add(hess, constraint.d2eval_dx2(x));
    });
    return hess;
  }


}

export {TotalJoint, Joint}
