import {Joint} from "./joint";
import * as mjs from "mathjs";
import {JointTypes} from "../constants";

class FixJoint extends Joint {
  constructor(bodyId, pos, angle) {
    super();
    this.bodyId = bodyId;
    this.fixedPos = mjs.matrix([pos.x, pos.y]);
    this.fixAngle = angle;
    this.weight = 1000;
  }

  getJointType() {
    return JointTypes.FIX;
  }

  eval(x) {
    let p = super.getPos(x, this.bodyId);
    let theta = super.getAngle(x, this.bodyId);

    let s1 = mjs.subtract(p, this.fixedPos);
    let s2 = theta - this.fixAngle;
    return this.weight*0.5*(mjs.dot(s1, s1) + s2*s2);
  }

  deval_dx(x) {
    let grad = mjs.matrix(mjs.zeros(x.size()[0]));

    let xp = super.getPos(x, this.bodyId);

    let c1 = (mjs.subtract(xp, this.fixedPos));

    let c2 = this.getAngle(x, this.bodyId) - this.fixAngle;
    // (p(x) - fix)*
    grad.subset(mjs.index([3*this.bodyId, 3*this.bodyId+1]), mjs.multiply(c1, this.weight));
    grad.subset(mjs.index(3*this.bodyId + 2), this.weight*c2);
    return grad
  }

  d2eval_dx2(x) {
    let hess = mjs.matrix(mjs.zeros([x.size()[0], x.size()[0]]));
    let idx = 3*this.bodyId;

    hess.subset(mjs.index(idx, idx), this.weight);
    hess.subset(mjs.index(idx+1, idx+1), this.weight);
    hess.subset(mjs.index(idx+2, idx+2), this.weight);
    return hess;
  }
}

export {FixJoint}
