import * as mjs from 'mathjs'

function grad_descent(x, obj) {
  let x_opt = mjs.clone(x);
  let val = obj.eval(x_opt);
  console.log("val before optimizing: ", val);
  let N_STEPS = 10;
  console.log("optimizing ", N_STEPS , " steps now");

  let i; let lambda = 1e-2;

  for (i = 0; i < N_STEPS; i++) {
    let grad = obj.deval_dx(x_opt);
    let multpl = mjs.multiply(grad, lambda)
    x_opt = mjs.subtract(x_opt, multpl);
  }
  val = obj.eval(x_opt);
  console.log("val after optimizing: ", val);

  return x_opt;
}

function newton_opt(x, obj) {
  let x_opt = mjs.clone(x);
  let val = obj.eval(x_opt);
  console.log("val before optimizing: ", val);
  if (val < 1e-3) return x;
  let N_STEPS = 10, N_LINE_SEARCH = 10;
  console.log("optimizing ", N_STEPS , " steps now");

  let i, j; let alpha = 1;
  let reg = 1;

  for (i = 0; i < N_STEPS; i++) {
    let grad = obj.deval_dx(x_opt);
    let hes = obj.d2eval_dx2(x_opt);
    hes = mjs.add(hes, mjs.matrix(mjs.diag(new Array(x.size()[0]).fill(reg))));

    let search_dir = mjs.lusolve(hes, grad);
    search_dir = mjs.reshape(search_dir, [x.size()[0]]);

    let curr_best = obj.eval(x_opt);
    let found_better = false;
    for (j = 0; j < N_LINE_SEARCH; j++) {
      let multpl = mjs.multiply(search_dir, alpha);
      let x_line = mjs.subtract(x_opt, multpl);
      let new_val = obj.eval(x_line);
      if (new_val < curr_best) {
        x_opt = x_line;
        found_better = true;
        break;
      } else {
        alpha /= 2;
        continue;
      }
    }
    if (!found_better) return x_opt;

  }
  val = obj.eval(x_opt);
  console.log("val after optimizing: ", val);

  return x_opt;
}

function grad_descent_line_search(x, obj) {
  let x_opt = mjs.clone(x);
  let val = obj.eval(x_opt);
  console.log("val before optimizing: ", val);
  let N_STEPS = 10, N_LINE_SEARCH = 10;
  console.log("optimizing ", N_STEPS , " steps now");

  let i, j; let alpha = 1;

  for (i = 0; i < N_STEPS; i++) {
    let grad = obj.deval_dx(x_opt);
    let curr_best = obj.eval(x_opt);
    for (j = 0; j < N_LINE_SEARCH; j++) {
      let multpl = mjs.multiply(grad, alpha);
      let x_line = mjs.subtract(x_opt, multpl);
      let new_val = obj.eval(x_line);
      if (new_val < curr_best) {
        x_opt = x_line;
        break;
      } else {
        alpha /= 2;
      }
    }
  }
  val = obj.eval(x_opt);
  console.log("val after optimizing: ", val);

  return x_opt;
}


function random_optimizer(x, obj) {
  let x_opt = mjs.clone(x);
  let val = obj.eval(x_opt);
  console.log("val before optimizing: ", val);
  let N_STEPS = 10;
  console.log("optimizing ", N_STEPS , " steps now");

  let i; let lambda = 1e-2;

  for (i = 0; i < N_STEPS; i++) {
    let k = mjs.randomInt(x_opt.size()[0]);
    let k_idx = mjs.index(k);
    let old_val = x_opt.subset(k_idx);
    let new_val = old_val + mjs.random(-1,1);
    x_opt.subset(k_idx, new_val);
    let new_opt_val = obj.eval(x_opt);
    if (new_opt_val < val) {
      val = new_opt_val;
    } else {
      x_opt.subset(k_idx, old_val);
    }
  }
  val = obj.eval(x_opt);
  console.log("val after optimizing: ", val);

  return x_opt;
}

export {grad_descent, random_optimizer, grad_descent_line_search, newton_opt}
