import * as THREE from "three";
import * as mjs from "mathjs";

class Body {
  static bodyCounter = 0;
  constructor() {
    this.width = 200;
    this.height = 30;
    this.depth = 2;
    const geometry = new THREE.BoxGeometry(this.width, this.height, this.depth);
    const material = new THREE.MeshBasicMaterial( {color: 0x000000} );
    this.cube = new THREE.Mesh( geometry, material );
    this.z_rotation = 0;
    const hoverMaterial = new THREE.MeshBasicMaterial({color: 0xff0000, opacity: 0.5, transparent: true});
    this.hoverCube = new THREE.Mesh( geometry, hoverMaterial );
    this.bodyIdx = Body.bodyCounter++;


    // body state dependant constraint list
    this.constraints = [];
  }

  getPos() {
    return new THREE.Vector3(this.cube.position.x, this.cube.position.y, this.cube.position.z);
  }

  setPosition(x, y, z) {
    this.cube.position.set(x, y, z);
  }

  optimize_gradient_descent() {
    let grad = mjs.matrix(mjs.zeros(3));

    let c;
    for (c of this.constraints) {
      grad = mjs.add(grad, c.dc_dbody());
    }

    grad = grad.valueOf();

    let x = this.getPos().x;
    let y = this.getPos().y;
    let theta = this.getTheta();

    let lambda = 1e-1;
    x = x - lambda*grad[0];
    y = y - lambda*grad[1];
    theta = theta - lambda*grad[2];
    this.setPosition(x, y, this.getPos().z);
    this.setTheta(theta);
  }


  get2dRotMatrix() {
    let theta = this.getTheta();
    let cosA = mjs.cos(theta);
    let sinA = mjs.sin(theta);
    let m =  mjs.matrix([[cosA, -sinA], [sinA, cosA]]);
    return m;
  }

  get_drot_dxi() {
    let theta = this.getTheta();
    let cosA = mjs.cos(theta);
    let sinA = mjs.sin(theta);
    let m =  mjs.matrix([[-sinA, -cosA], [cosA, -sinA]]);
    return m;
  }

  toWorld(pLocal) {
    let p = mjs.matrix([pLocal.x, pLocal.y]);
    let r1 = mjs.multiply(this.get2dRotMatrix(), p);
    let res = mjs.add(this.getPos(), r1);
    return res;
  }

  // returns dp/dxi where xi = [pos(bodyId).x, pos(bodyId).y, theta(bodyId)]
  // rest of the dxi's would be zero anyway
  // result = [dp/x dp/dy dp/dtheta]
  dWorld_dxi(pLocal) {
    let p = mjs.matrix([pLocal.x, pLocal.y]);
    let drot_dxi = this.get_drot_dxi();
    let v1 = mjs.multiply(drot_dxi, p).valueOf();
    return mjs.matrix([[1, 0, v1[0]],
      [0, 1, v1[1]]]);
  }


  getLocal(world) {

  }

  getState() {
    return [this.getPos().x, this.getPos().y, this.getTheta()];
  }

  getTheta() {
    return this.cube.rotation.z;
  }

  setTheta(theta) {
    this.cube.rotation.set(0,0, theta);
  }

  add(sceneContainer) {
    sceneContainer.scene.remove(this.hoverCube);
    sceneContainer.scene.add(this.cube);
    console.log(this.cube);
    sceneContainer.links.push(this);
  }

  showHover(scene) {
    scene.add(this.hoverCube);
  }

  removeHover(scene) {
    scene.remove(this.hoverCube);
  }
}

export {Body}
