import {Joint} from "../mechanisms/joint";
import {SceneReactor} from "./scene";
import {HingeReactor} from "./hingeReactor";
import {FixConstraintReactor} from "./fixReactor";
import {doing, JointTypes} from "../constants";
import {gui} from "dat.gui";

class JointReactor extends SceneReactor {
  constructor(sceneContainer) {
    super(sceneContainer);

    this.jointReactors = [
      new HingeReactor(this.sceneContainer),
      new FixConstraintReactor(this.sceneContainer)
    ]

    this.options = {
      jointType: JointTypes.FIX
    }
  }

  doing_type() {
    return doing.Joints;
  }


  initDatGui(gui) {
    let folder = gui.addFolder("Joint Config");
    folder.open();
    folder.add(this.options, 'jointType', [JointTypes.FIX, JointTypes.HINGE]);
  }


  mouseDown(event) {
    this.jointReactors.forEach(r => {
      if (r.jointType === this.options.jointType) r.mouseDown(event);
    });
  }

  mouseMove(event, isDragging) {
    this.jointReactors.forEach(r => {
      if (r.jointType === this.options.jointType) r.mouseMove(event, isDragging);
    });
  }

  preRender(gui_options) {
    this.jointReactors.forEach(r => {
       r.preRender(gui_options);
    });
  }

  switchDesignOptions(gui_options) {
    this.jointReactors.forEach(r => {
      r.switchDesignOptions(gui_options);
    });
  }

  mouseUp(event) {
    this.jointReactors.forEach(r => {
      if (r.jointType === this.options.jointType) r.mouseUp(event);
    });
  }

}

export {JointReactor}
