import {SceneReactor} from "./scene";
import {doing} from "../constants";
import {Body} from "../mechanisms/link";


class BodyReactor extends SceneReactor {

    constructor(sceneContainer) {
        super(sceneContainer);
        this.body = new Body();
        this.isAddingBodies = true;
    }

    doing_type() {
      return doing.BODIES;
    }

   initDatGui(gui) {
        gui.addFolder("Body")
    }

    preRender(gui_options) {
        if (!gui_options.is_addBodies())
            return;

    }

    switchDesignOptions(new_doing) {

        if (new_doing === doing.BODIES) {
          this.isAddingBodies = true;
          this.body.showHover(this.scene);
        } else {
          this.isAddingBodies = false;
          this.body.removeHover(this.scene);
        }
    }

    mouseDown(event) {
      if (!this.isAddingBodies) return;

      if (event.is_right_click) {
        this.sceneContainer.links[this.sceneContainer.links.length -1].setTheta(Math.PI/8);
        return;
      }

      this.body.hoverCube.position.set(event.x, event.y, 0);
      this.body.cube.position.set(event.x, event.y, 0);
      this.body.add(this.sceneContainer);

      this.body = new Body();
      this.body.hoverCube.position.set(event.x, event.y, 0);
      this.body.cube.position.set(event.x, event.y, 0);
      this.body.showHover(this.scene);
    }

    mouseMove(event, isDragging) {
      if (!this.isAddingBodies) return;

      this.body.hoverCube.position.set(event.x, event.y, 0);
      this.body.cube.position.set(event.x, event.y, 0);
    }
}

export {BodyReactor}
